<template>
  <v-container>
    <v-card class="mx-auto" max-width="1200" outlined elevation="20" :loading="loading">
      <v-list-item three-line>
        <v-list-item-content>
          <v-row justify="space-around">
            <v-col cols="12">
              <v-img :src="require('../assets/sportsfest_banner-event-registration.png')" class="details-banner-top p-0"
                contain />
            </v-col>
          </v-row>
          <v-row justify="space-around">
            <v-col cols="11">
              <v-list-item-title class="form-title text-h5 mb-1 mt-3 text-center">
                <p style="font-size: 42px;color:#34b8df;"><b>Register</b></p>
              </v-list-item-title>
              <v-form ref="form" v-model="valid" class="mt-10" id="formSaveConcern" :disabled="loading">
                <v-container class="containerform">
                  <h2>Employee</h2>
                  <v-row class="mb-2">
                    <v-col cols="12" md="6">
                      <v-text-field v-model="employeeNumber" ref="employeeNumber" prefix="OBPI-" label="Employee Number"
                        :rules="employeeNumberRules" @change="getEmployeeDetails()"
                        hint="Make sure that you are registered in sports fest"></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field v-model="employeeName" label="Name" disabled></v-text-field>
                    </v-col>
                    <v-col cols="12" md="12">
                      <v-text-field v-model="teamName" label="Team Name" disabled></v-text-field>
                    </v-col>
                  </v-row>
                  <div v-if="is_companion">
                    <h2>Companion</h2>
                    <v-row>
                      <v-col cols="12" md="6">
                        <v-select :items="age_items" ref="companion_age_1" label="SELECT"
                          v-model="companion_age_1"></v-select>
                      </v-col>
                      <v-col cols="12" md="6" v-if="is_companion_2">
                        <v-select :items="age_items" label="SELECT" v-model="companion_age_2"></v-select>
                      </v-col>
                    </v-row>
                  </div>
                  <h2>Are you going to attend the first day of Sportsfest(June 24, 2023)?</h2>
                  <v-row>
                    <v-col cols="6">
                      <v-radio-group v-model="is_coming_first_day" @change="is_going_first_day()"
                        :rules="is_coming_first_day_rules" ref="is_coming_first_day">
                        <v-radio v-for="n in ['Yes', 'No']" :key="n" :label="n" :value="n" :ref="n"></v-radio>
                      </v-radio-group>
                    </v-col>
                  </v-row>
                  <div v-if="food_first" class="mb-5">
                    <h2>Food</h2>
                    <v-row>
                      <v-col cols="6">
                        <v-card>
                          <v-img :src="require('../assets/d1-set-a.png')" height="auto" class="grey darken-4"></v-img>
                          <v-card-title>
                            <v-row align="center" justify="center" class="mb-1">
                              <v-btn depressed color="primary" :disabled="d1_set_a" rounded @click="food_choice(1)">
                                SELECT
                              </v-btn>
                            </v-row>

                          </v-card-title>
                        </v-card>
                      </v-col>
                      <v-col cols="6">
                        <v-card>
                          <v-img :src="require('../assets/d1-set-b.png')" height="auto" class="grey darken-4"></v-img>
                          <v-card-title>
                            <v-row align="center" justify="center" class="mb-1">
                              <v-btn depressed color="primary" :disabled="d1_set_b" rounded @click="food_choice(2)">
                                SELECT
                              </v-btn>
                            </v-row>
                          </v-card-title>
                        </v-card>
                      </v-col>
                    </v-row>
                  </div>

                  <div class="mt-3">
                    <h2>Are you going to attend the second day of Sportsfest(July 8, 2023)?</h2>
                    <v-row>
                      <v-col cols="6">
                        <v-radio-group v-model="is_coming_second_day" @change="is_going_second_day()"
                          :rules="is_coming_second_day_rules" ref="is_coming_second_day">
                          <v-radio v-for="n in ['Yes', 'No']" :key="n" :label="n" :value="n" :ref="n"></v-radio>
                        </v-radio-group>
                      </v-col>
                    </v-row>
                    <div v-if="food_second">
                      <h2>Food</h2>
                      <v-row>
                        <v-col cols="6">
                          <v-card>
                            <v-img :src="require('../assets/d2-set-a.png')" height="auto" class="grey darken-4"></v-img>
                            <v-card-title>
                              <v-row align="center" justify="center" class="mb-1">
                                <v-btn depressed color="primary" :disabled="d2_set_a" rounded @click="food_choice(3)">
                                  SELECT
                                </v-btn>
                              </v-row>

                            </v-card-title>
                          </v-card>
                        </v-col>
                        <v-col cols="6">
                          <v-card>
                            <v-img :src="require('../assets/d2-set-b.png')" height="auto" class="grey darken-4"></v-img>
                            <v-card-title>
                              <v-row align="center" justify="center" class="mb-1">
                                <v-btn depressed color="primary" :disabled="d2_set_b" v-model="d2_set_b" rounded
                                  @click="food_choice(4)">
                                  SELECT
                                </v-btn>
                              </v-row>
                            </v-card-title>
                          </v-card>
                        </v-col>
                      </v-row>
                    </div>
                  </div>


                  <v-row class="mt-5">
                    <v-col cols="12" align="center" justify="center">
                      <v-btn color="#34b8df" class="mr-4" @click="doCreate()" id="btnRegister" :loading="loading" x-large>
                        Submit
                      </v-btn>
                    </v-col>
                  </v-row>

                  <v-snackbar v-model="snackbar" :color="details_status ? 'red' : 'green'">
                    <p style="font-size: 20px" class="text-center">
                      {{ message }}
                    </p>
                    <template v-slot:action="{ attrs }">
                      <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
                        Close
                      </v-btn>
                    </template>
                  </v-snackbar>
                </v-container>
              </v-form>
            </v-col>
          </v-row>
          <v-divider></v-divider>
        </v-list-item-content>
      </v-list-item>
    </v-card>
  </v-container>
</template>
<script>
import VueMask from "v-mask";
import { ref } from "vue";
import { mapActions, mapState } from "vuex";
import formRules from "@/helpers/formRules";
import subjects from "@/data/subjects.json";
export default {
  name: "MainPage",
  components: { VueMask },
  data: () => ({
    teamName: "",
    d1_food_choice: "",
    d2_food_choice: "",
    d2_set_b: false,
    d2_set_a: false,
    d1_set_a: false,
    d1_set_b: false,
    is_companion: false,
    is_companion_2: false,
    food_first: false,
    food_second: false,
    is_coming_first_day: "",
    is_coming_first_day_rules: [(v) => !!v || "This field is required"],
    is_coming_second_day: "",
    is_coming_second_day_rules: [(v) => !!v || "This field is required"],
    age_items: ['Below 12 years old', '12 years old and above'],
    rules: formRules,
    snackbar: false,
    details_status: false,
    message: "",
    valid: false,
    employeeNumber: "",
    employeeID: "",
    employeeName: "",
    companion_age_2: "",
    companion_age_1: "",
    agree: "",
    employeeNumberRules: [(v) => !!v || "Employee number is required"],
    checkboxRules: [
      (v) =>
        !!v ||
        "Please read and agree to the Terms and Conditions and Privacy by checking the box.!",
    ],
  }),
  mounted() {
    this.$refs.employeeNumber.focus();
  },
  computed: {
    ...mapState("register", [
      "loading",
      "employee_details_response",
      "errors",
      "error_status_code",
    ]),
  },

  methods: {
    ...mapActions("register", [
      "doRegister",
      "doGetEmployeeDetails",
    ]),
    food_choice(val) {
      if (val == 1) {
        this.d1_food_choice = 'Set A'
        this.d1_set_a = true
      } else if (val == 2) {
        this.d1_food_choice = 'Set B'
        this.d1_set_b = true
      } else if (val == 3) {
        this.d2_food_choice = 'Set A'
        this.d2_set_a = true
      } else if (val == 4) {
        this.d2_food_choice = 'Set B'
        this.d2_set_b = true
      }
    },
    is_going_first_day() {
      if (this.is_coming_first_day == "Yes") {
        this.food_first = true
      } else {
        this.food_first = false
      }
    },
    is_going_second_day() {
      if (this.is_coming_second_day == "Yes") {
        this.food_second = true
      } else {
        this.food_second = false
      }
    },
    async doCreate() {
      if (this.$refs.form.validate()) {
        if (
          this.employeeNumber.includes("N/A") ||
          this.employee_details_response == undefined ||
          this.employee_details_response == "" ||
          this.employee_details_response == null
        ) {
          this.snackbar = true;
          this.message = "Please register first";
          this.details_status = true;
        } else {
          if (!this.loading) {
            await this.doRegister(
              {
                'EmployeeID': this.employeeID,
                'foodType': this.d1_food_choice,
                'isgoing': this.is_coming_first_day,
              }
            );
            this.snackbar = true;
            if (this.employee_details_response.isSuccess) {
              if (!this.employee_details_response.isSuccess) {
                this.snackbar = true;
                this.message = "You are already registered";
                this.details_status = true;
              } else {
                this.details_status = false;
                this.message =
                  "Thank you";
                this.$refs.form.reset();
              }

            } else {
              if (this.error_status_code == 500) {
                this.details_status = true;
                this.message = "Something went wrong";
              } else if (this.error_status_code == 422) {
                this.details_status = true;
                this.message = "Invalid input";
              } else if (this.error_status_code == 550) {
                this.details_status = true;
                this.message = "Failed to send concern/s";
              } else if (this.error_status_code == 404) {
                this.details_status = true;
                this.message = "Something went wrong";
              } else {
                this.details_status = true;
                this.message = "Employee already registered";
              }
            }
          }
        }
      }
    },
    async getEmployeeDetails() {
      this.details_status = true;
      this.$store.commit("register/setEmployeeDetailsResponse", []);
      await this.doGetEmployeeDetails(this.employeeNumber);
      if (!this.errors) {
        this.is_companion = false
        this.food_first = false
        this.food_second = false
        this.snackbar = true;
        this.message = "Something went wrong";
        this.details_status = true;
      } else {
        if (this.employee_details_response != null) {
          if (this.employeeNumber.includes("N/A")) {
            this.snackbar = true;
            this.message = "Invalid employee number";
            this.details_status = true;
            this.$refs.form.reset()
          } else {
            this.employeeName = this.employee_details_response.firstName + ' ' + this.employee_details_response.lastName;
            this.teamName = this.employee_details_response.teamName
            this.employeeID = this.employee_details_response.employeeID
            if (this.employee_details_response.companiom != 0) {
              this.is_companion = true
              if (this.employee_details_response.companiom == 2) {
                this.is_companion_2 = true
              }
            }
          }
        } else {
          this.is_companion = false
          this.food_first = false
          this.food_second = false
          this.snackbar = true;
          this.message = "Invalid employee number";
          this.details_status = true;
          this.$refs.form.reset()
        }
      }
    },
  },
};
</script>
