const url = process.env.VUE_APP_API_URL
import Vuex from 'vuex'
export default ({
  namespaced: true,
  state: {
    employee_details_response: [],
    errors: [],
    loading: false,
    error_status_code: 0,
    error_ticketing: []

  },
  getters: {
  },
  mutations: {
    setErrors(state, val) {
      state.errors = val
    },
    setErrors_Status_Code(state, val) {
      state.error_status_code = val
    },
    setLoading(state, val) {
      state.loading = val
    },
    setEmployeeDetailsResponse(state, val) {
      state.employee_details_response = val
    },
    setErrorTicketing(state, val) {
      state.error_ticketing = val
    },
  },
  actions: {
    async doRegister({ commit, dispatch }, form) {
      commit("setLoading", true)
      commit("setErrors_Status_Code", 0)
      // const form_data = new FormData();

      // for (var key in data) {
      //   form_data.append(key, data[key])
      // }
      try {
        const response = await fetch(`http://10.0.0.249:8091/api/sfregister`, {
          method: 'POST',
          body: JSON.stringify(form),
          headers:{
            'Accept' : 'application/json',
            'Content-Type' : 'application/json'
          }
        })
        const res = await response.json()

        if (!response.ok) {
          throw { response, res }
        } else {
          if(res){
            commit("setEmployeeDetailsResponse", res)
          }else{
            commit("setErrors", errorStatus)
            commit("setErrors_Status_Code", 500)
          }
        }
      } catch (error) {
        const errorStatus = error.response.status
        if (errorStatus == 422) {
          commit("setErrors", errorStatus)
          commit("setErrors_Status_Code", 422)
        } else if (errorStatus == 500) {
          commit("setErrors", errorStatus)
          commit("setErrors_Status_Code", 500)
        } else if (errorStatus == 550) {
          commit("setErrors", errorStatus)
          commit("setErrors_Status_Code", 550)
        }

      } finally {
        commit("setLoading", false)
      }
    },
   
    async doGetEmployeeDetails({ commit }, employeeId) {
      commit("setLoading", true)
      try {
        const response = await fetch("http://10.0.0.249:8091/api/sportsteam/getsportsteam?emp_id=" + 'OBPI-'+employeeId, {
          method: 'GET',
        })

        const res = await response.json()
        if (!response.ok) {
          throw { response, res }
        } else {
          if (res) {
            commit("setEmployeeDetailsResponse", res.data)
          }

        }
      } catch (errors) {
        //console.log(errors)
        commit("setErrors", errors) // Decoy error to detect success

      } finally {
        commit("setLoading", false)
      }
    }
  }
})